<template>
  <div id="broken-line" :style="size">
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="options" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from '@/components/ECharts'
export default {
  components: {
    ECharts
  },
  data: function () {
    return {
      year: 2000,
      options: {
        title: {
          text: '2000年深圳和广州各月新冠新增情况',
          textStyle: {
            color: "rgba(255,255,255,0.9)"
          },
          left: "center",
          top: "5px"
        },
        color: ['#00f2f1', "#ed3f35"],
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          top: "15%",
          right: "10%",
          // 里面的名字要与serious里面的名字保持一致
          data: ['深圳新冠', '广州新冠'],
          show: true,
          textStyle: {
            color: "rgba(255,255,255,0.6)"
          }
        },
        grid: {
          top: '30%',
          left: '10%',
          right: '10%',
          bottom: '4%',
          show: true,
          borderColor: "#012f4a",
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
          axisTick: {
            show: false
          },
          axisLabel: {
            color: "rgba(255,255,255,0.7)"
          },
          axisLine: {
            show: false
          }
        },
        yAxis: {
          type: 'value',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.3)"
            }
          },
          splitLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.3)"
            }
          },
          axisLabel: {
            textStyle: {
              color: "rgba(255,255,255,0.6)",
              fontSize: "12"
            }
          },
        },
        series: [
          {
            name: '深圳新冠',
            type: 'line',
            // stack: 'Total',
            data: [120, 132, 101, 134, 90, 230, 210, 120, 132, 101, 134, 90],
            smooth: true
          },
          {
            name: '广州新冠',
            type: 'line',
            // stack: 'Total',
            data: [220, 182, 191, 234, 290, 330, 310, 182, 191, 234, 290, 330],
            smooth: true
          }
        ]
      },
      size: {
        width: '100%',
        height: '3.875rem'
      }
    }
  },
  mounted () {
    setInterval(() => {
      this.year++;
      this.options.title.text = `${this.year}年深圳和广州各月新冠新增情况`
      this.options.series[0].data.forEach((item, i) => {
        this.options.series[0].data[i] = item + Math.floor(Math.random() * 200) - 200
        // if (this.options.series[0].data[i] < 0) {
        //   this.options.series[0].data[i] = 0
        // }
      })
      this.options.series[1].data.forEach((item, i) => {
        this.options.series[1].data[i] = item + Math.floor(Math.random() * 88) - 50
        // if (this.options.series[0].data[i] < 0) {
        //   this.options.series[0].data[i] = 0
        // }
      })
      this.options = { ...this.options }
    }, 2000)
  }
}
</script>

<style scoped>

#broken-line{
	/* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255,255,255,0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop{
    width: 100%;
    height: 10px;
    position: absolute;
    top: 0;
    left: 0
}

.dragRectTop::before{
    content: "";
    width: 10px;
    height: 10px;
    border-left: 2px solid skyblue;
    border-top: 2px solid skyblue;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
}

.dragRectTop::after{
    content: "";
    width: 10px;
    height: 10px;
    border-right: 2px solid skyblue;
    border-top: 2px solid skyblue;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
}

.dragRectBottom{
    width: 100%;
    height: 10px;
    position: absolute;
    bottom: 0;
    left: 0
}

.dragRectBottom::before{
    content: "";
    width: 10px;
    height: 10px;
    border-left: 2px solid skyblue;
    border-bottom: 2px solid skyblue;
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
}

.dragRectBottom::after{
    content: "";
    width: 10px;
    height: 10px;
    border-right: 2px solid skyblue;
    border-bottom: 2px solid skyblue;
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
}
</style>