<template>
  <div id="app">
    <div class="header">
      <h1>大数据可视化</h1>
      <div class="showtime">当前时间为：{{nowTime}}</div>
    </div>
    <div class="charts">
      <div class="left">
        <bar></bar>
        <broken-line></broken-line>
        <pie></pie>
      </div>
      <div class="mid">
        <div class="title" style="font-family: electronicFont">
          <div class="number">
            <div class="borderTop"></div>
            <span>012255</span>
            <span></span>
            <span>100225</span>
            <div class="borderbottom"></div>
          </div>
          <div class="desc">
            <span>新冠确诊人数</span>
            <span>新冠疑似人数</span>
          </div>
        </div>
        <Map></Map>
      </div>
      <div class="right">
        <ver-bar></ver-bar>
        <broken-line2></broken-line2>
        <pie2></pie2>
      </div>
    </div>
  </div>
</template>

<script>
import ECharts from '@/components/ECharts'
import Bar from '@/components/Bar'
import BrokenLine from '@/components/BrokenLine'
import Pie from '@/components/Pie'
import VerBar from '@/components/VerBar'
import BrokenLine2 from '@/components/BrokenLine2'
import Pie2 from '@/components/Pie2'
import Map from '@/components/Map'



export default {
  name: 'app',
  data: function () {
    return {
      nowTime: ""
    }
  },
  components: {
    ECharts,
    Bar,
    BrokenLine,
    Pie,
    VerBar,
    BrokenLine2,
    Pie2,
    Map
  },
  mounted () {
    setInterval(() => {
      this.nowTime = this.getNowTime()
    }, 1000)
  },
  methods: {
    getNowTime () {
      let myDate = new Date()
      let year = myDate.getFullYear()
      let month = myDate.getMonth() + 1
      let date = myDate.getDate()
      let hour = myDate.getHours() < 10 ? "0" + myDate.getHours() : myDate.getHours()
      let minute = myDate.getMinutes() < 10 ? "0" + myDate.getMinutes() : myDate.getMinutes()
      let second = myDate.getSeconds() < 10 ? "0" + myDate.getSeconds() : myDate.getSeconds()
      return `${year}年${month}月${date}日${hour}:${minute}:${second}`
    }
  }
}
</script>

